.btn a {
  color: white;
}

.btn {
  margin-bottom: 20px;
}

.search-form input[type=text] {
  margin-top: 20px;
  width: 80%;
  margin: 0 auto;
  position: relative;
  margin-top: -20px;
  height: 5vh;
  font-size: 1.6vw;
}

.header {
  background-image: url('https://res.cloudinary.com/dfypp9c6h/image/upload/v1608191114/kita-project/Kita-header_c7uzwt.jpg');
  background-size: cover;
  height: 50vh;
}

.header img {
  z-index: -1;
}

.tagline {
  color: white;
  width: 40%;
  line-height: 3.8vw;
  font-size: 3vw;
  font-weight: bold;
  padding: 20px 0 0 30px;
}

.center-list {
  margin: 0 auto;
}

.h1-kitas {
  font-weight: bold;
  margin: 10px 0 20px 0;
}

.applied {
  color: red;
}

.kita-list {
  font-size: 1.2rem;
}

@media screen and (max-width: 900px) {
  .tagline {
    font-size: 35px;
    width: 80%;
    line-height: 1.2em;
  }

  .search-form input[type=text] {
    font-size: 16px;
  }
  
  .search-results {
    width: 95%;
  }

}